.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgb(230, 226, 248);
  box-shadow: 0px 0px 10px #cbd5e1;
  z-index: 1000;
}

.header-container {
  padding: 25px;
  max-width: 1280px;
  margin: 0px auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-left img {
  width: 50px;
}

.header-right ul {
  display: flex;
  align-items: center;
  gap: 0px 50px;
}

.header-right ul a {
  color: #475569;
  text-decoration: none;
  font-weight: 500;
  position: relative;
}

.header-right ul a:before {
  position: absolute;
  content: "";
  width: 0;
  height: 4px;
  background-color: #38bdf8;
  bottom: -10px;
  transition: 0.3s ease-in-out;
}

.header-right ul a:hover:before {
  width: 90%;
}

.header-link-hightlight {
  padding: 10px 30px;

  background: #6366f1;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.222);
  border-radius: 5px;
}
.header-link-hightlight a {
  color: #fff !important;
}


.header-link-hightlight a:hover::before {
    display: none;
}