.social {
  max-width: 1280px;
  margin: 0px auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 60px;
  padding: 0px 30px;
  margin-bottom: 40px;
}

.social-item {
  background-color: #f1f5f94b;
  min-height: 200px;
  border-radius: 10px;
  padding: 20px;
  transition: 0.2s all ease-out;
}

.social-item:hover {
  transform: translateY(-10px);
}

.social-item-heading {
  margin-top: 10px;
  color: #6366f1;
  display: flex;
  align-items: center;
  gap: 0px 4px;
}

.social-item-desc {
  margin-top: 10px;
  color: #475569;
}

.social-item a {
  display: inline-block;
  padding: 15px 40px;
  margin-top: 20px;
  background: #6366f1;
  color: #fff;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.222);
  border-radius: 5px;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .social {
    grid-template-columns: repeat(1, 1fr);
  }
}
