.intro {
  max-width: 1280px;
  margin: 0px auto;
  min-height: 400px;
  margin-top: 150px;
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.intro-thumb {
  position: absolute;
}
.intro-thumb img {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: 10px solid transparent;
}

.intro .border-box {
  width: 270px;
  height: 270px;
  /* border: 10px solid #6366f1; */
  border-radius: 50%;
  position: absolute;
  background: linear-gradient(to right, rgb(107, 11, 231), rgb(16, 229, 253));
  z-index: -1;
  animation: border_box_rotate 4s infinite linear;
}

@keyframes border_box_rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.intro-heading h2 {
  font-size: 25px;
  color: #2563eb;
}

.intro-heading {
  margin-top: 300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
}

.intro-heading-subtitle {
  margin-top: 10px;
  color: #64748b;
  font-weight: bold;
  font-size: 15px;
}

.intro-button {
  margin-top: 20px;
  padding: 10px 20px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.124);
  background: #3b82f6;
  border-radius: 5px;
  color: #fff;
  transition: 0.3s all ease-out;
}

.intro-button:hover {
  transform: scale(1.1);
}
